'use client';

import Link from 'next/link';
import Image from 'next/image';
import { Star, Flame } from 'lucide-react';
import { Button } from '@/components/ui/button';
import { VoteButton } from '@/components/vote-button';
import { useEffect, useState } from 'react';
import { moviesApi } from '@/lib/api';
import { getMovieCertification } from '@/lib/movie-utils';

interface Movie {
  id: string;
  title: string;
  rating: number;
  duration: number;
  language: string;
  genre: string[];
  poster_url: string;
}

export function TrendingMoviesSection() {
  const [movies, setMovies] = useState<Movie[]>([]);
  const [loading, setLoading] = useState(true);

  useEffect(() => {
    const fetchMovies = async () => {
      try {
        const allMovies = await moviesApi.getAll();
        // Filter for specific trending movies to match design
        const trendingTitles = ['3 Idiots', 'RajaKumara', 'Kis Kisko Pyaar Karoon'];
        const trending = allMovies.filter((m: any) => trendingTitles.includes(m.title));

        // Use matching movies or sliced fallback
        setMovies(trending.length > 0 ? trending : allMovies.slice(4, 7));
      } catch (error) {
        console.error("Failed to fetch trending movies:", error);
      } finally {
        setLoading(false);
      }
    };

    fetchMovies();
  }, []);

  if (loading) {
    return <div className="py-16 text-center">Loading trending movies...</div>;
  }

  return (
    <section className="py-16 bg-gradient-to-b from-background to-muted/30">
      <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8">
        {/* Header */}
        <div className="flex items-center justify-between mb-12">
          <div className="flex items-center gap-3">
            <Flame className="w-8 h-8 text-accent" />
            <h2 className="text-3xl md:text-4xl font-bold text-foreground">Re-Release Voting Arena</h2>
          </div>
          <Link href="/trending" className="text-accent hover:text-accent/80 font-semibold">
            View All →
          </Link>
        </div>

        {/* Movies Grid */}
        <div className="grid grid-cols-1 md:grid-cols-3 gap-6">
          {movies.map((movie) => (
            <div
              key={movie.id}
              onClick={() => window.location.href = `/movies/${movie.id}`}
              className="group rounded-xl overflow-hidden bg-gradient-to-b from-[#111827] to-[#0F172A] border border-[#374151] hover:border-[#2563EB] shadow-lg hover:shadow-2xl transition-all duration-300 transform hover:-translate-y-2 cursor-pointer"
            >
              {/* Image with Vote Badge */}
              <div className="relative h-80 overflow-hidden bg-muted">
                <Image
                  src={movie.poster_url || "/placeholder.svg"}
                  alt={movie.title}
                  fill
                  className="object-cover group-hover:scale-110 transition-transform duration-300"
                />
                {/* Overlay */}
                <div className="absolute inset-0 bg-gradient-to-t from-black/60 to-transparent" />

                {/* Vote Badge */}
                <div className="absolute bottom-4 left-4 flex items-center gap-2 bg-accent text-accent-foreground px-3 py-2 rounded-full">
                  <Flame className="w-4 h-4 fill-current" />
                  <span className="font-bold">{Math.floor(Math.random() * 5000) + 2000} votes</span>
                </div>
              </div>

              {/* Content */}
              <div className="p-4 space-y-4">
                <div>
                  <h3 className="font-bold text-lg text-foreground line-clamp-2">{movie.title}</h3>
                  <p className="text-xs text-muted-foreground mt-1">
                    {getMovieCertification(movie.title)} • {movie.language}
                  </p>
                </div>

                <div className="flex items-center justify-between">
                  <div className="flex items-center gap-1 bg-accent text-accent-foreground px-2 py-1 rounded">
                    <Star className="w-3 h-3 fill-current" />
                    <span className="text-xs font-bold">{movie.rating}</span>
                  </div>
                  <VoteButton movieId={movie.id} compact />
                </div>

                <Link href={`/movies/${movie.id}`}>
                  <Button className="w-full bg-accent hover:bg-accent/90 text-accent-foreground font-bold rounded-lg">
                    Book Tickets
                  </Button>
                </Link>
              </div>
            </div>
          ))}
        </div>
      </div>
    </section>
  );
}
